<template>
  <div class="my">
    <div class="my-bg">
      <van-nav-bar>
        <template #title>
          <div class="my-title">我 的</div>
        </template>
        <template #left>
          <div class="left-icon" @click="notice">
            <img src="../../assets/images/xiaoxi.png" alt class="auto-img" />
          </div>
        </template>
        <template #right>
          <div class="right-icon" @click="showPopup">
            <img src="../../assets/images/shezhi.png" alt class="auto-img" />
          </div>
        </template>
      </van-nav-bar>
      <van-popup
        v-model="show"
        position="left"
        :style="{ width: '70%', height: '100%' }"
      >
        <div class="personal">
          <div class="list-box">
            <van-cell title="头像">
              <div class="avatar fr">
                <img class="auto-img" :src="img" alt />
                <van-uploader class="uploader-box" :after-read="headimg" />
              </div>
            </van-cell>
            <van-cell title="性别">
              <div class="fr">
                <van-radio-group v-model="radio" direction="horizontal">
                  <van-radio name="1">男</van-radio>
                  <van-radio name="2">女</van-radio>
                </van-radio-group>
              </div>
            </van-cell>
            <van-cell title="手机号">
              <div class="fr">{{ phone }}</div>
            </van-cell>
            <van-cell title="昵称">
              <div class="fr">{{ nickName }}</div>
            </van-cell>
            <div class="desc-box">
              <div class="desc-title">简介</div>
              <div>
                <textarea
                  v-model="synopsis"
                  class="text-area"
                  placeholder="这家伙很懒，什么都没有留下"
                  @change="modifySynopsis"
                ></textarea>
              </div>
            </div>
          </div>
        </div>
      </van-popup>
      <div class="my-card">
        <div class="my-head clearfix">
          <div class="head-img fl">
            <div class="head-img-top">
              <img :src="img" alt class="auto-img headT" />
            </div>
          </div>
          <div class="head-name fl" v-if="isName">
            <span @click="goLogin">注册</span>|
            <span @click="goRegister">登录</span>
          </div>
          <div class="name-center fl" v-else>
            <div class="P">{{ nickName }}</div>
            <div class="P">{{ synopsisUser }}</div>
          </div>
        </div>
        <div class="my-care-etc clearfix">
          <div class="care-etc fl">
            <div class="care-etc-text">关注</div>
            <div class="care-etc-number">{{ vermicelliCare.vermicelli }}</div>
          </div>
          <div class="care-etc fr">
            <div class="care-etc-text">粉丝</div>
            <div class="care-etc-number">{{ vermicelliCare.care }}</div>
          </div>
        </div>
      </div>
    </div>
    <div class="my-list">
      <div class="other" @click="surplus">
        <van-notice-bar mode="link" color="black">
          <div class="other-text">书券余额</div>
        </van-notice-bar>
      </div>
      <div class="other" @click="Vip">
        <van-notice-bar mode="link" color="black">
          <div class="other-text">VIP中心</div>
        </van-notice-bar>
      </div>
      <div class="other" @click="square">
        <van-notice-bar mode="link" color="black">
          <div class="other-text">评论广场</div>
        </van-notice-bar>
      </div>
      <div class="other" @click="about">
        <van-notice-bar mode="link" color="black">
          <div class="other-text">关于漫画</div>
        </van-notice-bar>
      </div>
      <div class="other" @click="feedback">
        <van-notice-bar mode="link" color="black">
          <div class="other-text">帮助反馈</div>
        </van-notice-bar>
      </div>
      <div class="other" @click="signOut">
        <van-notice-bar mode="link" color="black">
          <div class="other-text">退出登录</div>
        </van-notice-bar>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/less/my.less";
export default {
  data() {
    return {
      //展示侧边栏
      show: false,
      // 性别
      radio: "1",
      //手机号
      phone: "",
      //昵称
      nickName: "",
      //简介
      synopsis: "",
      fileList: [],
      // 头像
      img: require("../../assets/images/headimg.png"),
      //用户信息
      userInfo: {},
      //登录后切换名字
      isName: true,
      // 粉丝关注
      vermicelliCare: {},
      //用户简介
      synopsisUser: "这家伙很懒，什么也没留下",
      // 作品类别数据
      typeData: [],
      // 作品状态数据
      statusData: [],
      // 地域区分数据
      regionData: [],
    };
  },

  created() {
    //获取个人信息
    this.getPersonalInformation();
    //展示个人信息(头像/简介)
    let userInfo = JSON.parse(localStorage.getItem("userInfo"));
    if (userInfo) {
      this.img = userInfo.img;
      this.synopsis = userInfo.synopsis;
      this.synopsisUser = userInfo.synopsis;
    }

    //展示粉丝关注数量
    //展示个人信息(头像/简介)
    let vermicelliCare = JSON.parse(localStorage.getItem("vermicelliCare"));
    if (vermicelliCare) {
      this.vermicelliCare.vermicelli = vermicelliCare.vermicelli;
      this.vermicelliCare.care = vermicelliCare.care;
    }
  },
  methods: {
    //展示侧边栏
    showPopup() {
      this.show = true;
    },

    //通知
    notice(){
      this.$router.push({name:"Notice"})
    },

    //登录
    goRegister() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      setTimeout(() => {
        this.$toast.clear();
        this.$router.push({ name: "Register" });
      }, 1000);
    },

    //注册
    goLogin() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      setTimeout(() => {
        this.$toast.clear();
        this.$router.push({ name: "Login" });
      }, 1000);
    },

    //获取个人信息
    getPersonalInformation() {
      let Personal = JSON.parse(localStorage.getItem("token"));
      if (!Personal) {
        return;
      }
      //登录后切换名字
      this.isName = false;
      // 电话，名字信息
      this.phone = Personal.phone;
      this.nickName = Personal.niakName;
      //粉丝，关注
      let vermicelliCare = JSON.parse(localStorage.getItem("vermicelliCare"));
      if (vermicelliCare) {
        return;
      }
      let vermicelli = Math.floor(Math.random() * (100 - 1) + 1);
      let care = Math.floor(Math.random() * (100 - 1) + 1);

      if (!(this.vermicelliCare.vermicelli && this.vermicelliCare.care)) {
        this.vermicelliCare.vermicelli = vermicelli;
        this.vermicelliCare.care = care;
        localStorage.setItem(
          "vermicelliCare",
          JSON.stringify(this.vermicelliCare)
        );
      }
    },

    // 上传头像
    headimg(file) {
      let Personal = JSON.parse(localStorage.getItem("token"));
      if (!Personal) {
        return;
      }
      //获取文件类型
      let type = file.file.type.split("/")[1];
      let imgTypes = ["png", "gif", "jpg", "jpeg"];
      if (imgTypes.indexOf(type) === -1) {
        this.$notify({
          message: "图片类型只支持" + imgTypes.join(","),
          color: "#fff",
          background: "#5f56ff",
        });
        return;
      }
      //大小限制
      if (this.maxSize < file.file.size) {
        this.$notify({
          message: "上传文件大小不能超过" + this.maxSize / 1024 + "KB",
          color: "#fff",
          background: "#5f56ff",
        });
        return;
      }
      this.img = file.content;
      this.userInfo.img = file.content;
      this.userInfo.synopsis = this.synopsis;
      localStorage.setItem("userInfo", JSON.stringify(this.userInfo));
    },

    //修改简介
    modifySynopsis() {
      this.userInfo.img = this.img;
      this.userInfo.synopsis = this.synopsis;
      this.synopsisUser = this.userInfo.synopsis;
      localStorage.setItem("userInfo", JSON.stringify(this.userInfo));
    },

    //书券余额
    surplus() {
      this.$dialog({ message: "您的书券余额为零，赶紧去充值吧" });
    },

    //vip中心
    Vip() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      setTimeout(() => {
        this.$toast.clear();
        this.$router.push({ name: "Vip" });
      }, 1000);
    },

    //评论广场
    square() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      setTimeout(() => {
        this.$toast.clear();
        this.$router.push({ name: "Square" });
      }, 1000);
    },

    // 获取数据
    getdata(arr, datas) {
      arr.forEach((v) => {
        this.axios({
          mrthods: "GET",
          url: "http://47.114.147.221",
          params: {
            mhlb: v,
          },
        })
          .then((res) => {
            let resList = Number(res.data.list[0].number);
            datas.push(resList);
          })
          .catch(() => {});
      });
    },

    // 关于阅读
    about() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      //作品状态
      let statusArr = ["new", "hot", "wanjie", "lianzai"];
      this.getdata(statusArr, this.statusData);
      // 作品类别
      let typeArr = [
        "shaonianrexue",
        "wuxiagedou",
        "kongbulingyi",
        "danmeirensheng",
        "shaonvaiqing",
        "lianaishenghuo",
        "shenghuomanhua",
        "kehuanmohuan",
        "jingjitiyu",
        "baoxiaoxiju",
        "zhentantuili",
      ];
      this.getdata(typeArr, this.typeData);
        //地域区分
        let regionArr=[
          "dalu",
          "riben",
          "hangou",
          "xianggan",
          "taiwang",
          "oumei",
          "qita",
        ];
      this.getdata(regionArr, this.regionData);
      setTimeout(() => {
        this.$toast.clear();
        let opus=JSON.parse(localStorage.getItem("opus"));
        if(!opus){
          let opus=[];
          let datasan={
            statusData:this.statusData,
            typeData: this.typeData, 
            regionData: this.regionData
          }
          opus.push(datasan);
          localStorage.setItem("opus", JSON.stringify(opus));
        }else{
          let opus=[];
           let datasan={
            statusData:this.statusData,
            typeData: this.typeData, 
            regionData: this.regionData
          }
          opus.push(datasan);
          localStorage.setItem("opus", JSON.stringify(opus));
        }
        this.$router.push({
          name: "About",
        });
      }, 2000);
    },

    //帮助反馈
    feedback() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      setTimeout(() => {
        this.$toast.clear();
        this.$router.push({ name: "FeedBack" });
      }, 1000);
    },

    //退出登录
    signOut() {
      //判断是否登录
      let Personal = JSON.parse(localStorage.getItem("token"));
      if (!Personal) {
        this.$dialog({ message: "您未登录" });
        return;
      }
      this.$dialog
        .confirm({
          title: "退出登录",
          message: "您确定退出登录吗",
        })
        //确定退出登录
        .then(() => {
          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
            loadingType: "spinner",
          });

          setTimeout(() => {
            this.$toast.clear();
            //移除登录信息
            localStorage.removeItem("token");
            localStorage.removeItem("vermicelliCare");
            localStorage.removeItem("userInfo");
            //清除登录之后的各种信息
            this.img = require("../../assets/images/headimg.png");
            this.nickName = "";
            this.userInfo = {};
            this.isName = true;
            this.phone = "";
            this.synopsisUser = "";
            this.synopsis = "";
            this.vermicelliCare.vermicelli = "";
            this.vermicelliCare.care = "";
            this.radio = "1";
            this.$notify({ background: "#157efb", message: "退出登录" });
          }, 1000);
        })
        .catch(() => {
          this.$notify({ background: "#157efb", message: "取消退出登录" });
        });
    },
  },
};
</script>

<style lang="less" scoped>
</style>